;\n$font-size-big: 1.5rem;\n$font-size-bigger: 1.75rem;\n$font-size-mega: 2.125rem;\n$font-size-giga: 2.5rem;\n\n// spaces\n$zero: 0;\n$space-micro: 0.125rem;\n$space-smaller: 0.25rem;\n$space-small: 0.5rem;\n$space-one: 0.675rem;\n$space-slab: 0.75rem;\n$space-normal: 1rem;\n$space-two: 1.25rem;\n$space-medium: 1.5rem;\n$space-large: 2rem;\n$space-larger: 3rem;\n$space-jumbo: 4rem;\n$space-mega: 6.25rem;\n\n// font-weight\n$font-weight-feather: 100;\n$font-weight-light: 300;\n$font-weight-normal: 400;\n$font-weight-medium: 500;\n$font-weight-bold: 600;\n$font-weight-black: 700;\n\n//Navbar\n$nav-bar-width: 14.375rem;\n$header-height: 3.5rem;\n\n$woot-logo-padding: $space-large $space-two;\n\n// Colors\n$color-woot: #1f93ff;\n$color-gray: #6e6f73;\n$color-light-gray: #999a9b;\n\n$color-border: var(--s-75);\n$color-border-light: var(--s-50);\n$color-border-dark: var(--s-100);\n\n$color-background: var(--s-50);\n$color-background-light: var(--s-25);\n\n$color-white: #fff;\n$color-body: #3c4858;\n$color-heading: #1f2d3d;\n$color-extra-light-blue: #f5f7f9;\n\n$primary-color: $color-woot;\n$secondary-color: #5d7592;\n$success-color: #44ce4b;\n$warning-color: #ffc532;\n$alert-color: #ff382d;\n\n$masked-bg: rgba(0, 0, 0, .4);\n\n// Color-palettes\n\n$color-primary-light: #c7e3ff;\n$color-primary-dark: darken($color-woot, 20%);\n\n// Thumbnail\n$thumbnail-radius: 2.5rem;\n\n// chat-header\n$conv-header-height: 2.5rem;\n\n// Inbox List\n\n$inbox-thumb-size: 3rem;\n\n\n// Snackbar default\n$woot-snackbar-bg: #323232;\n$woot-snackbar-button: #ffeb3b;\n\n$swift-ease-out-duration: .4s !default;\n$swift-ease-out-function: cubic-bezier(0.37, 0, 0.63, 1) !default;\n$swift-ease-out: all $swift-ease-out-duration $swift-ease-out-function !default;\n\n// Ionicons\n$ionicons-font-path: '~ionicons/fonts';\n\n// Transitions\n$transition-ease-in: all 0.250s ease-in;\n\n:root {\n  --dashboard-app-tabs-height: 2.4375rem;\n}\n","\n@import '~dashboard/assets/scss/mixins';\n\n.notification--title {\n  @apply text-sm m-0 text-slate-800 dark:text-slate-100;\n}\n\n.notifications-table {\n  > tbody {\n    > tr {\n      @apply cursor-pointer;\n\n      &:hover {\n        @apply bg-slate-50 dark:bg-slate-800;\n      }\n\n      &.is-active {\n        @apply bg-slate-100 dark:bg-slate-700;\n      }\n\n      > td {\n        &.conversation-count-item {\n          @apply pl-6 rtl:pl-0 rtl:pr-6;\n        }\n      }\n\n      &:last-child {\n        @apply border-b-0;\n      }\n    }\n  }\n}\n\n.is-unread {\n  @apply font-semibold;\n}\n\n.notifications--loader {\n  @apply text-base flex items-center justify-center p-10;\n}\n\n.notification--unread-indicator {\n  @apply w-2.5 h-2.5 rounded-full bg-woot-500 dark:bg-woot-500;\n}\n\n.notification--created-at {\n  @apply text-slate-700 dark:text-slate-200 text-xs;\n}\n\n.notification--type {\n  @apply text-xs;\n}\n\n.thumbnail--column {\n  @apply w-[3.25rem];\n}\n\n.timestamp--column {\n  @apply min-w-[9.125rem] text-right;\n}\n\n.notification-contant--wrap {\n  @apply flex-col max-w-[31.25rem];\n}\n\n.notification--message-title {\n  @apply text-slate-700 dark:text-slate-100;\n}\n"]}]);
